
@font-face {
    font-family: "myFirstFont";
    src: url("../../../assets/Neucha-Regular.ttf");
    /*src: url("../../../assets/AmaticSC-Regular.ttf");*/
}

.intro {
    /*font-family: 'Rock Salt';*/
    /*font-family: 'Days One', sans-serif;*/
    font-family: 'myFirstFont', sans-serif;
    color: var(--white);
    position: fixed;
    font-size: 1.5rem;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(0,0,0,0.8);
    z-index: 2;
    display: flex;
    justify-content: center;
    flex-flow: row wrap;
    padding: 10px;
}


.actions {
    text-align: center;
    width: 100%;
    max-width: 450px;

    margin-top: 60px;
}

.right {
    width: 110px;
    position: absolute;
    right: 10px;
    bottom: 80px;
}

.rightPointer {
    margin-left: 78px;
}

.center {
    position: absolute;
    width: 130px;
    bottom: 20px;
    left: 50%;
    margin-left: -65px;
    text-align: center;
    cursor: pointer;
}

.circle {
    margin-top: 10px;
    width: 65px;
    margin-left: auto;
    margin-right: auto;
    border-radius: 50px; 
    animation-name: buttonFade;
    animation-duration: 3.2s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-out;
}

.circle img {
    max-width: 100%;
}

@keyframes buttonFade {
    0% {background: rgba(198, 84, 84, 0.8);}
    50%  {background: transparent;}
    100%  {background: rgba(198, 84, 84, 0.8);}
}