.body {
    display: grid;
    grid-template-columns: 300px 1fr;
    grid-gap: 20px;
    text-align: left;
}

.body .details , .body .contacts {
    padding: 20px;
    background: #fff;
    box-shadow: 0 2px 2px rgba(0,0,0,0.2);
}

.tags {
    padding-left: 10px;
}

.tag {
    font-size: 12px;
}

.right-arrow{
    position: absolute;
    left: 5px;
    top:45%;
    width: 30px;
}



.loading {
    height: 100vh;
    background: rgba(0,0,0,0.41);
    display: flex;
    align-items: center;
    justify-content: center;
}

.closed{
    position: absolute;
    top:5px;
    right: 5px;
    color: black;
}

.random{
    background: #FFC300;
    padding: 13px;
    display: flex;
}
.randomicon{
    height: 50px;
    width: 57px;
}

.randomtext{
    font-size: 15px;
    color: #020105c7;
    margin: 6px 10px;
}

.wrap {
    height: 100vh;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    box-shadow: 0 120px 90px 100px rgba(0,0,0,0.4) inset;
}

.content {
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.content-inner {
    flex: 1;
    flex-direction: column;
    padding: 30px 20px 20px 20px;
    justify-content: space-between;
    color: var(--white);
    display: flex;
}

.detailed-rating {
    position: absolute;
    right: 30px;
    top: 30px;
    z-index: 2;
}

.detailed-content :global(.slick-list) {
    background: linear-gradient(0deg, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0) 40%, rgba(196,50,172,0) 60%, rgba(0,0,0,0.7) 100%);
}

.content-top {
    justify-content: space-between;
    display: flex;
    width: 100%;
    margin-bottom: 20px;
}


.colored {
    position: sticky;
  height: 5px;
  border-top: 0;
  background: #fff;
    margin: 0;
  background-image: linear-gradient(to right, #c4e17f, #c4e17f 12.5%, #f7fdca 12.5%, #f7fdca 25%, #fecf71 25%, #fecf71 37.5%, #f0776c 37.5%, #f0776c 50%, #db9dbe 50%, #db9dbe 62.5%, #c49cde 62.5%, #c49cde 75%, #669ae1 75%, #669ae1 87.5%, #62c2e4 87.5%, #62c2e4);
  padding-right: 50%;
  background-origin: content-box;
  background-size: 200%;
  animation-name: colored;
  animation-duration: 5s;
  animation-iteration-count: infinite;
  animation-timing-function: steps(8);
}

@keyframes colored {
  from {background-position: 0% 0%;}  
    to {background-position: 200% 0%;}
}

.title {
    font-weight: 900;
    font-size: 1.62rem;
    line-height: 25px;
}

.workHours {
    font-size: 0.8rem;
}

.col {
    width: calc(100% - 47px);
}

.right-col {
    width: 50px;
    margin-left: 10px;
}

.rating {
    color: #FFC91A;
    font-size: 0.75rem;
    font-weight: 900;
}

.rating div {
    margin-bottom: 3px;
}

.images {
    display: flex;
    flex-flow: row wrap;
    position: relative;
}

.content-bottom {
    margin-top: auto;
    justify-content: space-between;
    /*display: flex;*/
    width: 102%;
    display: grid;
    align-items: flex-end;
    grid-gap: 5px;
    grid-template-columns: repeat(3, 1fr);
}

.content-bottom-item {
    position: relative;
}

.content-bottom-item button{
    margin: 0 auto;
    text-align: center;
}

.visited {
    display: flex;
    align-items: center;
    margin-top: 10px;
}


.cost {
    font-weight: 500;
    font-size: 0.75rem;
    margin-bottom: 60px;
}

.images img {
    max-width: 100%;
    background: #2b354f;
}

.meta {
    position: absolute;
    left: 20px;
    bottom: 20px;
    padding: 10px 20px;
    background-color: #af79f5;
}

.details ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.details li {
    margin-bottom: 10px;
    display: flex;
    flex-flow: row wrap;
}

.refreshBtn {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.contacts {
    display: grid;
    grid-template-columns: 1fr 400px;
    grid-gap: 20px;
}

.contacts-data-item a {
    color: #2b354f;
}

.whiteblock {
    padding: 20px;
    background: var(--white);
    border-radius: 8px;
}

.map {
    margin-top: 40px;
}

.compare-card-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 30px;
}

.compare-card {
    position: relative;

    display: flex;
    flex-wrap: wrap;

    box-sizing: border-box;
    padding: 46px 20px 70px;

    text-align: center;

    border-radius: 8px;
    background-color: var(--white);
    box-shadow: 0 12px 20px rgba(0, 0, 0, 0.1);
}

.compare-card--top {
    width: 100%;
}

.compare-card-photo--inner {
    white-space: nowrap;
    display: flex;

    font-size: 0;
}

.compare-card-photo--image {
    position: relative;
    z-index: 2;

    display: inline-block;
    overflow: hidden;

    box-sizing: border-box;
    width: 22px;
    height: 22px;
    margin-right: -11px;

    border: 3px solid var(--white);
    border-radius: 100px;
}

.avatar {
    position: relative;

    margin: 0 auto;
}

.avatar-img-wrap {
    overflow: hidden;

    border-radius: 100px;
}

.avatar-img {
    object-fit: fill;

    max-width: 100%;
}

.mark {
    padding: 10px;
    border-radius: 5px;
    position: absolute;
    transform: rotate(-20deg);
    top: 180px;
    left: 30px;
    z-index: 2;
    font-size: 2rem;
    font-weight: 500;
    text-align: center;
    width: 160px;
    border: 2px solid;
}

.markPositive {
    color: var(--green);
    border-color: var(--green);
}

.markNegative {
    color: var(--red);
    border-color: var(--red);
}

.recommendation-over {
    background-image: url("../../assets/img/recommendation/subscribeBg.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    text-align: center;
    padding: 30px;
    color: #fff;
    font-weight: 300;
    height: 100vh;
}

.rate {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0,0,0,0.8);
    padding: 30px;
    text-align: center;
    color: var(--white);
    display: flex;
    flex-direction: column;
    height: 100vh;
    z-index: 20;
}

.detailed-block {
    padding: 15px;
    font-size: 1rem;
    border-radius: 10px;
    background: rgba(0,0,0,0.7);
    font-weight: 500;
    margin-bottom: 15px;
    position: relative;
    color: var(--white);
}
.detailed-block-main {
    padding: 15px;
    font-size: 12px;
    border-radius: 10px;
    background: rgba(0,0,0,0.7);
    font-weight: 500;
    margin: 10px;
    bottom: 50px;
    position: absolute;
}

.detailed-block:last-of-type {
    margin-bottom: 60px;
}

.cross {
    width: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 30px;
    font-size: 1.5rem;
    cursor: pointer;
    margin-right: 5px;
    margin-left: auto;
    border-radius: 3px;
    margin-bottom: 5px;
    background: rgba(255,255,255,0.6);
}

.share {
    width: 20px;
}

.rate-title {
    font-size: 1.87rem;
    margin: 0 0 20px 0;
}

.link-white {
    color: var(--white);
}

.categoryBtn {
    position: absolute;
    right: 0;
    bottom: 0;
    background-image: url("../../assets/img/recommendation/white-bg.svg");
    width: 70px;
    height: 60px;
    background-color: transparent;
    padding: 0;
    border: none;
    margin-left: auto !important;
    display: block;
}

.profileBtn {
    position: absolute;
    left: 0;
    bottom: 0;
    background-image: url("../../assets/img/recommendation/white-bg-2.svg");
    width: 77px;
    height: 58px;
    background-color: transparent;
    padding: 0;
    border: none;
    margin-left: auto !important;
    display: block;
}

.link-pink {
    color: #EC2D93;
    margin-top: auto;
}

.subsribeBtn {
    color: var(--white);
    font-weight: 600;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 100px;
    overflow: hidden;
    background-color: rgba(255,255,255, 0.2);
    border: 2px #fff dashed;
    padding: 5px 10px;
    margin: 0 auto;
    line-height: 1;
}

.catMenu {
    position: absolute;
    padding: 10px 5px;
    border-radius: 50px;
    background: #fff;
    text-align: center;
    bottom: 70px;
    right: 4px;
}

.catIcon {
    font-size: 12px;
    font-weight: 500;
    margin-bottom: 15px;
    color: #888888;
}

.categoryBtnActive .burger {
    transform: rotate(90deg);
}

.categoryBtn img {
    display: block;
    margin: 0;
    width: 30px;
    transition: transform 0.15s ease-out;
    position: absolute;
    right: 10px;
    bottom: 15px;
}

.catIcon img {
    margin: 0 auto;
}

.catIcon:last-of-type {
    margin-bottom: 0;
}

.subsribeBtn:disabled {
    opacity: 0.6;
}

.rate-faces {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
}

.rate-faces-sep {
    margin: 0 10px;
}

.rate-face-btn {
    width: 55px;
    height: 55px;
    background: transparent;
    padding: 0;
    border: none;
}

@media (min-width: 1250px) {
    .images {
        min-height: 500px;
    }
}

@media (max-width: 1100px) {
    .body {
        grid-auto-columns: 200px 1fr;
    }
}

@media (max-width: 1024px) {
    .contacts {
        display: block;
    }

    .contacts-data {
        margin-bottom: 20px;
    }

    :global(.openlayers-map) {
        min-width: 0;
    }
}

@media (max-width: 767px) {

    .refreshBtn {
        position: static;
        transform: translate(0,0);
        margin-top: 20px;
    }

    .refreshBtn button {
        width: 100%;
        background-color: #af79f5;
    }

    .meta {
        position: relative;
        background: transparent;
        padding: 0 30px 0 0;
        width: calc(100% - 90px);
        text-align: left;
        left: 0;
        bottom: 0;
    }

    .body {
        display: block;
    }

    .details {
        margin-bottom: 20px;
    }

    .contacts {
        display: block;
    }

    .map img {
        width: 100%;
    }

    .images {
        margin: -30px auto 40px auto;
        background: #FFFFFF;
        box-shadow: 0 4px 4px rgba(83, 41, 244, 0.08);
        border-radius: 3px;
        display: flex;
        overflow: hidden;
        padding: 10px;
    }

    .images img {
        width: 80px;
        height: 80px;
        margin-right: 10px;
        object-fit: cover;
    }

    .directions {
        width: 20px;
        position: absolute;
        right: 10px;
       top: 0;
    }
}

.detailed-list {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    overflow: auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}

.detailed-type {
    width: 150px;
    color: #fff;
    font-size: 1rem;
    font-weight: 500;
    position: absolute;
    z-index: 2;
    top: 30px;
    left: 20px;
}

.nav {
    width: 30px;
    height: 30px;
    background: rgba(255,255,255,0.7);
    border-radius: 100px;
    position: absolute;
    bottom: 20px;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: center;
}

.nav svg {
    width: 20px;
    height: 20px;
}

.nav-prev {
    left: 20px;
}

.nav-next {
    right: 20px;
}